<template>

  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-form :inline="true" :model="form" ref="form">
        <el-form-item label="菜单">
          <el-select-tree default-expand-all ref="tree" v-model="searchItem.menuId"  :treeData="menuTree" :propNames="defaultProps" clearable placeholder="请选择菜单" :filter-node-method="filterNode">
          </el-select-tree>
        </el-form-item>
        <el-form-item label="菜单列编号" size="small">
          <el-input style="width: 140px;" size="small" v-model="searchItem.colCode" placeholder="请输入编号" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="菜单列名称">
          <el-input  style="width: 140px;" size="small" v-model="searchItem.colName" placeholder="请输入名称" icon="search"></el-input>
        </el-form-item>
  <!--       <el-form-item label="角色名称">
          <el-input  style="width: 140px;" size="small" v-model="searchItem.name" placeholder="请输入角色名称" icon="search"></el-input>
        </el-form-item> -->
        <el-form-item>
          <el-button type="primary" size="small"  @click="search">搜索</el-button>
        </el-form-item>
        <el-form-item style="float: right;">
          <el-button type="primary" icon="plus" @click="addCol()">添加菜单列</el-button>
        </el-form-item>
      </el-form>
    </h3>
    <el-row slot="body" :gutter="24" style="margin-bottom: 20px;">
    <el-table :data="tableData" style="width: 100%;">
      <el-table-column prop="colName" label="菜单列名称" width="180"></el-table-column>
      <el-table-column prop="colCode" label="菜单列编码" width="180"></el-table-column>
      <el-table-column prop="menuId" label="菜单ID"></el-table-column>
      <!-- <el-table-column prop="phone" label="手机号"></el-table-column> -->
      <el-table-column label="操作">
        <template scope="scope">
          <el-popover ref="popoverDetail" placement="top-start" width="200" trigger="click">
            <el-form :model="form">
              <el-form-item label="角色编号：" style="margin-bottom: 0px;">
                <span v-model="form.no">12121</span>
              </el-form-item>
              <el-form-item label="角色名称：" style="margin-bottom: 0px;">
                <span v-model="form.name">琪哥</span>
              </el-form-item>
              <el-form-item label="角色描述：" style="margin-bottom: 0px;">
                <span type="textarea" v-model="form.remarks">真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅</span>
              </el-form-item>
            </el-form>
          </el-popover>
          <el-button size="small" icon="document" v-popover:popoverDetail>查看</el-button>
          <el-button size="small" icon="edit" @click="addCol(scope.$index)">编辑</el-button>
          <el-button size="small" type="danger" icon="delete" @click="deleteRole(scope.$index, tableData)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-row>
  </imp-panel>

</template>
<script>

  import panel from "../../components/panel.vue"
  import selectTree from "../../components/selectTree.vue"
  import treeter from "../../components/treeter"


  import * as api from "../../api"

  export default {
    mixins: [treeter],
    components: {
      'imp-panel': panel,
      'el-select-tree': selectTree,
    },
    // watch: {
    //   filterText(val){
    //     this.$refs.tree.filter(val);
    //   }
    // },
    data(){
      return {
        // filterText: '',
        form: {
          id: null,
          no: '',
          name: '',
          remarks: '',
        },
        searchItem: {
          colCode: "",
          colName: "",
          menuId: ""
        },
        menuTree: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },

        tableData: [],
      }
    },
    methods: {
      filterNode(value, data){
        if(!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      addCol(index){
        console.log("index === "+ index);
        if(index == undefined){
          this.$router.push({path: 'userAddOrEdit'});
        } else {
          this.$router.push({path: 'userAddOrEdit', query: {id: this.tableData[index].id}});
        }
      },
      deleteRole(index, rows){
        // 如果eclipse开发环境，后台获取数据进行渲染
        if( api.CONTEXT === "./power-web") {
          var roleId = rows[index].id;
          var params = new URLSearchParams();
          params.append("roleCode", this.form.no);
          this.$http.post(api.USER_DELETE, params).then(res => {
            var data = res.data;
            var isSuccess = data.success;
            if(data.success) {
              rows.splice(index, 1);
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            } else {
              this.$message.error('删除失败');
            }
          })
        } else {
        // 如果是本地环境，假数据判断
          var data = {"data":null,"success":true,"message":"删除成功"};
          var isSuccess = data.success;
          if(data.success) {
            rows.splice(index, 1);
            this.$message({
              message: '删除成功',
              type: 'success'
            });
          } else {
            this.$message.error('删除失败');
          }
        }
      },
      search(){
        var params = new URLSearchParams();
        var needSearch = false;
        if(this.searchItem.colCode) {
          params.append("colCode", this.searchItem.colCode);
          needSearch = true;
        } else if(this.searchItem.colName) {
          params.append("colName", this.searchItem.colName);
          needSearch = true;
        } else if(this.searchItem.menuId) {
          params.append("menuId", this.searchItem.menuId);
          needSearch = true;
        }
        if(needSearch) {
          this.$http.get(api.MENUCOL_SELECT + "?" + params).then(res => {
            console.log(res.data);
            if(res.data.success){
              if(res.data.data.total > 0) {
                this.tableData = res.data.data.data;
                this.$message("数据查询成功");
              } else {
                this.tableData = [];
                this.$message("暂无数据");
              }
            } else {
              this.tableData = [];
              this.$message("查询失败");
            }
          })
        }
        // this.load(params);
        // console.log(this.filterText);
      },
      changeDataType(data, parentId) {
        var dataClone = [];
        for (var i = .0; i < data.length; i++) {
          // dataClone = cons
        }
        var tree = [];
        var temp;
        for (var i = 0; i < data.length; i++) {
            if (data[i].parentId == parentId) {
                var obj = data[i];
                temp = this.changeDataType(data, data[i].id);
                if (temp.length > 0) {
                 obj.children = temp;
                }
                tree.push(obj);
            }
        }
        return tree;
      },
      load(params) {
        if(api.CONTEXT === "./power-web") {
          this.$http.get(api.MENU_SELECT).then( res => {
            console.log(res.data);
            var data = res.data.data.data;
            for(var i=0; i < data.length; i++ ) {
              data[i].label = data[i].menuName;
              data[i].name = data[i].menuName;
            }
            this.menuTree = this.changeDataType(data, 0);
          })
        }
        // if (api.CONTEXT === "./power-web") {
        //   this.$http.get(api.USER.SELECT, params).then( res => {
        //     console.log(res.data);
        //     this.tableData = res.data.data.data;
        //   })
        // } else {
        //   var data = {"data":{"data":[{"id":7,"isDeleted":0,"userCode":"002","userName":"SS","password":"SSS","realName":"SSS","sex":null,"birthday":null,"phone":"SSS","address":null,"deptId":null,"remark":null},{"id":5,"isDeleted":0,"userCode":"002","userName":"SS1","password":"SSS","realName":"SSS","sex":null,"birthday":null,"phone":"SSS","address":null,"deptId":null,"remark":null},{"id":3,"isDeleted":0,"userCode":"001","userName":"aaa1","password":"aaa","realName":"张三","sex":null,"birthday":null,"phone":"15888888888","address":null,"deptId":null,"remark":null},{"id":1,"isDeleted":0,"userCode":"01","userName":"aaa","password":"bbb","realName":"老张","sex":1,"birthday":"1959-10-01 00:00:00","phone":null,"address":null,"deptId":0,"remark":""}],"total":4,"nowpage":0,"pagesize":6,"rowStart":0,"rowEnd":6,"order":"desc","sort":"id","condition":null},"success":true,"message":"成功"};
        //   this.tableData = data.data.data;
        // }
      }
    },
    created(){
      this.load();
    }
  }
</script>

<style>
  .render-content {
    float: right;
    margin-right: 20px
  }

  .render-content i.fa {
    margin-left: 10px;
  }

  .render-content i.fa:hover{
    color: #e6000f;
  }

  .select-tree .el-scrollbar.is-empty .el-select-dropdown__list {
    padding: 0;
  }

  .select-tree .el-scrollbar {
    border: 1px solid #d1dbe5;
  }

  .select-tree .el-tree{
    border:0;
  }

</style>



