<template>
  <imp-panel :title="title">
    <el-form ref="form" :model="form" label-width="80px" :rules="rules">
      <el-form-item label="用户编号" prop="no">
        <el-input v-model="form.userCode"></el-input>
      </el-form-item>
      <el-form-item label="名称" prop="name">
        <el-input v-model="form.userName"></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="name">
        <el-input v-model="form.realName"></el-input>
      </el-form-item>
      <el-form-item label="生日">
        <el-input v-model="form.birthday"></el-input>
      </el-form-item>
      <el-form-item label="性别">
        <el-input v-model="form.sex"></el-input>
      </el-form-item>
      <el-form-item label="手机号">
        <el-input v-model="form.phone"></el-input>
      </el-form-item> 
      <el-form-item label="密码">
        <el-input v-model="form.password"></el-input>
      </el-form-item>  
      <el-form-item label="地址">
        <el-input v-model="form.address"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input type="textarea" v-model="form.remarks"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" @click="onEditSubmit" v-if="form.title==='编辑用户'">保存</el-button>
        <el-button type="primary" @click="onSubmit('form')" v-else>添加</el-button>
        <el-button type="primary" @click="reset('form')">重置</el-button>
        <el-button type="primary" @click="returnList">返回</el-button>
      </el-form-item>
    </el-form>
  </imp-panel>
</template>
<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        title: "",
        form: {
          userCode: "",
          userName: "",
          realName: "",
          birthday: "",
          sex: "",
          phone: "",
          password: "",
          address: "",
          remarks: ""
        },
        submitData: {
          roleCode: '',
          roleName: '',
          sysId: '0'
        },
        // 校验规则
        rules: {
          no: [
            { required: true, message: '请输入角色编号', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在3到5个字符', trigger: 'blur' }
          ],
          type: [
            { required: true, message: "请选择角色类型", trigger: "change"}
          ],
          name: [
            { required: true, message: "请输入角色名称", trigger: 'blur' }
          ]
        }
      }
    },
    created(){
      this.loadData();
    },
    methods: {
      onSubmit(formName){
        // if(this.$route.query && this.$route.query.id) {
        //   submitApi = api.USER_UPDATE;
        // } else {
        //   submitApi = api.USER_INSERT;
        // }
        this.$refs[formName].validate((valid) => {
          if(valid) {
            var params = new URLSearchParams();
            params.append("roleCode", this.form.no);
            params.append("roleName", this.form.name);
            this.$http.post(api.USER_INSERT, params)
            .then(res => {
              console.log(res.data);
            })
          } else {
            return false;
          }
        })
      },
      onEditSubmit(){
        this.$refs[formName].validate((valid) => {
          if(valid) {
            var params = new URLSearchParams();
            params.append("roleCode", this.form.no);
            params.append("roleName", this.form.name);
            this.$http.post(api.USER_UPDATE, params)
            .then(res => {
              console.log(res.data);
            })
          } else {
            return false;
          }
        })
      },
      reset(formName){
        console.log(formName);
        // this.$refs[formName].resetFields();
        // this.form = {};
        this.form.userCode = "";
        this.form.userName = "";
        this.form.realName = "";
        this.form.birthday = "";
        this.form.sex = "";
        this.form.phone = "";
        this.form.password = "";
        this.form.address = "";
        this.form.remarks = "";
      },
      returnList(){
        this.$router.push({path: 'userList'});
      },
      loadData(){
        console.log(this.$route.query.id);
        if(this.$route.query && this.$route.query.id) {
          if(api.CONTEXT === "./power-web") {
            var params = new URLSearchParams();
            params.append("id", this.$route.query.id)
            this.$http.get(api.USER_SELECT,params).then( res => {
              console.log(res.data);
              var formData = res.data.data.data[0];
              if(formData) {
                this.form.userCode = formData.userCode;
                this.form.userName = formData.userName;
                this.form.realName = formData.realName;
                this.form.birthday = formData.birthday;
                this.form.sex = formData.sex;
                this.form.phone = formData.phone;
                this.form.password = formData.password;
                this.form.address = formData.address;
                this.form.remarks = formData.remarks;
              }
            })
          } else {
            var data = {"data":{"data":[{"id":7,"isDeleted":0,"userCode":"002","userName":"SS","password":"SSS","realName":"SSS","sex":null,"birthday":null,"phone":"SSS","address":null,"deptId":null,"remark":null}],"total":1,"nowpage":0,"pagesize":0,"rowStart":0,"rowEnd":10,"order":"desc","sort":"id","condition":null},"success":true,"message":"成功"};
            var formData = data.data.data[0];
            if(formData) {
              this.form.userCode = formData.userCode;
              this.form.userName = formData.userName;
              this.form.realName = formData.realName;
              this.form.birthday = formData.birthday;
              this.form.sex = formData.sex;
              this.form.phone = formData.phone;
              this.form.password = formData.password;
              this.form.address = formData.address;
              this.form.remarks = formData.remarks;
            }
          }
          this.title = "编辑用户";
        } else {
          this.title = "添加用户";
        }
      }
    }
  }
</script>



