import dashboard from "../pages/dashboard.vue";
import NotFoundView from "../components/404.vue";
import menuList from "../pages/sys/menu.vue";
import menuCol from "../pages/sys/menuCol.vue";
import role from "../pages/sys/role.vue";
import resource from "../pages/sys/resource.vue";
import login from "../pages/login.vue";
import app from "../App.vue";
import sysUser from "../pages/sys/user.vue";
import userAddOrEdit from "../pages/sys/userAddOrEdit.vue";
import colAddOrEdit from "../pages/sys/colAddOrEdit.vue";
import roleAddOrEdit from "../pages/sys/roleAddOrEdit.vue";
import resetPwd from "../pages/resetPwd.vue";
import company from "../pages/sys/company.vue"
import dept from "../pages/sys/dept.vue"
// Routes
const routes = [
  {path: '/login', component: login},
  {
    path: '/test', component: app, children: [
    {path: '*', component: NotFoundView}
  ]
  },
  {
    path: '', component: app, children: [
    {path: '/resetPwd', component: resetPwd},
    {path: '/index', component: dashboard},
    {path: '/sys/menuList', component: menuList},
    {path: '/sys/roleList', component: role},
    {path: '/sys/roleAddOrEdit', component: roleAddOrEdit},
    {path: '/sys/userList', component: sysUser},
    {path: '/sys/userAddOrEdit', component: userAddOrEdit},
    {path: '/sys/colAddOrEdit', component: colAddOrEdit},
    {path: '/sys/resource', component: resource},
    {path: '/sys/menuCol', component: menuCol},
    {path: '/sys/company', component: company},
    {path: '/sys/dept', component: dept}
  ]
  },
  {path: '*', component: NotFoundView}
]


export default routes




