<template>
  <imp-panel :title="form.id ? '编辑功能角色':'添加功能角色'">
    <el-form ref="form" :model="form" label-width="180px" :rules="rules">
      <el-form-item label="角色编号" prop="no">
        <el-input v-model="form.no"></el-input>
      </el-form-item>
      <!-- <el-form-item label="角色类型" prop="type">
        <el-select v-model="form.type" placeholder="请选择...">
          <el-option label="琪哥真帅" value="qige"></el-option>
          <el-option label="琪哥真好" value="qige1"></el-option>
        </el-select>
      </el-form-item> -->
      <el-form-item label="角色名称" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="角色描述">
        <el-input type="textarea" v-model="form.remarks"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" @click="onEditSubmit" v-if="form.id">保存</el-button>
        <el-button type="primary" @click="onSubmit('form')" v-else>添加</el-button>
        <el-button type="primary" @click="reset('form')">重置</el-button>
        <el-button type="primary" @click="returnList">返回</el-button>
      </el-form-item>
    </el-form>
  </imp-panel>
</template>
<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        form: {
          id: null,
          no: '',
          name: '',
          remarks: '',
        },
        submitData: {
          roleCode: '',
          roleName: '',
          sysId: '0'
        },
        rules: {
          no: [
            { required: true, message: '请输入角色编号', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在3到5个字符', trigger: 'blur' }
          ],
          type: [
            { required: true, message: "请选择角色类型", trigger: "change"}
          ],
          name: [
            { required: true, message: "请输入角色名称", trigger: 'blur' }
          ]
        }
      }
    },
    created(){
      this.loadData();
    },
    methods: {
      onSubmit(formName){

        this.$refs[formName].validate((valid) => {
          if(valid) {
            var params = new URLSearchParams();
            params.append("roleCode", this.form.no);
            params.append("roleName", this.form.name);
            this.$http.post("./power-web/power/role/insert", params)
            .then(res => {
              console.log(res.data);
              // this.form = res.data;
              // this.$confirm('添加成功, 是否返回列表?', '提示', {
              //   confirmButtonText: '确定',
              //   cancelButtonText: '取消',
              //   type: 'success'
              // }).then(() => {
              //   this.$router.push({path: 'userList'})
              // })
            })
          } else {
            return false;
          }
        })
      },
      reset(formName){
        this.$refs[formName].resetFields();
        // this.form.no = " ";
        // this.form.type = " ";
        // this.form.name = " ";
        // this.form.remarks = " ";
      },
      returnList(){
        this.$router.push({path: 'roleList', query: {id: "add"}});
      },
      // onEditSubmit(){
      //   this.$http.post(api.SYS_USER_UPDATE, this.form)
      //     .then(res => {
      //       this.form = res.data;
      //       this.$confirm('修改成功, 是否返回列表?', '提示', {
      //         confirmButtonText: '确定',
      //         cancelButtonText: '取消',
      //         type: 'success'
      //       }).then(() => {
      //         this.$router.push({path: 'userList'})
      //       })
      //     })
      // },
      loadData(){
        // console.log(!this.$route.query && !this.$route.query.id && this.$route.query.id = "add");
        // if(!this.$route.query && !this.$route.query.id && this.$route.query.id = "add") {

        // }
        // if (this.$route.query && this.$route.query != null && this.$route.query.id && this.$route.query.id != null) {
        //   this.form.id = this.$route.query.id;
        //   this.$http.get(api.SYS_USER_GET + "?id=" + this.form.id)
        //     .then(res => {
        //       this.form = res.data;
        //     })
        // }
      }
    }
  }
</script>



