<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-form :inline="true" :model="form" ref="form">
        <el-form-item label="公司名">
          <el-select v-model="searchItem.comId" filterable placeholder="请选择"  :filter-method="searchCompanyList">
            <el-option
              v-for="item in companyOptions"
              :key="item.id"
              :label="item.comName"
              :value="item.id">
            </el-option>
          </el-select>
          <!-- <el-input v-model="searchItem.comName" placeholder="请输入公司名称" icon="search"></el-input> -->
        </el-form-item>
        <el-form-item label="部门名称">
          <el-input v-model="searchItem.deptName" placeholder="请输入部门名称" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="联系人">
          <el-input v-model="searchItem.deptMan" placeholder="请输入角色名称" icon="search"></el-input>
        </el-form-item>
        <!-- <el-form-item label="联系方式">
          <el-input v-model="searchItem.contactWay" placeholder="请输入角色名称" icon="search"></el-input>
        </el-form-item> -->
        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
       <!--  <el-form-item style="float: right;">
          <el-button type="primary" icon="plus" @click="addRole('add')">添加功能角色</el-button>
        </el-form-item> -->
      </el-form>
    </h3>
    <el-row slot="body" :gutter="24" style="margin-bottom: 20px;">
      <div style="margin-left: 15px; margin-bottom: 15px;">
        <el-button type="primary" @click="addOrEdit('add')">新增</el-button>
        <el-button type="primary" @click="addOrEdit('edit')">编辑</el-button>
        <el-dialog :title="dialogFormName" :visible.sync="dialogFormVisible" size="tiny">
          <el-form :model="dialogForm"  :rules='rules' ref='dialogForm' >
            <el-form-item label="部门编码" :label-width="formLabelWidth" prop='deptCode'>
              <el-input v-model="dialogForm.deptCode" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="部门名称" :label-width="formLabelWidth" prop='deptName'>
              <el-input v-model="dialogForm.deptName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="联系人" :label-width="formLabelWidth" prop='deptMan'>
              <el-input v-model="dialogForm.deptMan" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="部门地址" :label-width="formLabelWidth" prop='deptAddress'>
              <el-input v-model="dialogForm.deptAddress" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="所属公司" :label-width="formLabelWidth" prop='comId'>
              <el-input v-model="dialogForm.comId" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="备注" :label-width="formLabelWidth">
              <el-input v-model="dialogForm.remark" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="submitForm('dialogForm')">确 定</el-button>
          </div>
        </el-dialog>
        <el-button type="primary" @click="updataStatus('enable')" >查看</el-button>
        <el-button type="primary" @click="updataStatus('enable')" :disabled='isEnable'>启用</el-button>
        <el-button type="primary" @click="updataStatus('freeze')" :disabled='isDisable'>禁用</el-button>
        <el-button type="primary" @click="deleteCompany()">删除</el-button>
        <el-button type="primary" @click="edit()">导出</el-button>
      </div>
    <el-table :data="tableData" style="width: 100%;" ref="multipleTable" border tooltip-effect="dark" :fit="true"  @selection-change='handleSelectionChange' height="445">
      <el-table-column type="selection" width="50" ></el-table-column> 
      <el-table-column prop="deptCode" label="部门编码" ></el-table-column>
      <el-table-column prop="deptName" label="部门名称" ></el-table-column>
      <el-table-column prop="deptMan" label="联系人" ></el-table-column>
      <el-table-column prop="deptAddress" label="部门地址" ></el-table-column>
      <el-table-column prop="comId" label="所属公司" ></el-table-column>
      <el-table-column prop="remark" label="备注" ></el-table-column>
    </el-table>
    <el-pagination
      style="margin-top: 15px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="nowpage"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-row>
  </imp-panel>

</template>
<script>

  import panel from "../../components/panel.vue"
  import selectTree from "../../components/selectTree.vue"
  import treeter from "../../components/treeter"
  import axios from "axios";
  import querystring from "querystring";
  import qs from 'qs';
  import $ from '../../jquery-2.2.4.min.js'


  import * as api from "../../api"
axios.interceptors.request.use((config) => {
    config.data = qs.stringify(config.data);
    return config;
}, function(error) {
    return Promise.reject(error);
});
  export default {
    mixins: [treeter],
    components: {
      'imp-panel': panel,
      'el-select-tree': selectTree,
    },
    data(){
      return {
        companyOptions: [],
        pagesize: 10,  // 每页10条数据
        total: 1000,  // 一共1000条数据
        nowpage: 1, // 当前第几页，后台是从1开始计数的
        form: {
          id: null,
          no: '',
          name: '',
          remarks: '',
        },
        searchItem: {
          comName: "",
          deptName:'',
          deptMan: ''
        },
        tableData: [],
        multipleTable: [],
        multipleSelection:[], // 表格多选数据
        dialogFormVisible: false, // 新增/编辑弹出层是否可见
        dialogFormName: '',  // 新增/编辑弹出层的title
        formLabelWidth: '80px', // 新增/编辑弹出层的宽度
        isDisable: true,    //"禁用"按钮是否禁用
        isEnable: true,   // "启用"按钮是否禁用
        dialogForm:{  // 弹出层表单数据
          id:'',
          deptCode: '',
          deptName: '',
          deptMan: '',
          deptAddress: "",
          comId: "",
          remark: ''
        },   // 新增/编辑弹出层的表单数据
        rules: {   //表单校验规则
          deptCode: [
            { required: true, message: '请输入部门编码', trigger: 'blur' },
            { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
          ],
          deptName: [
            { required: true, message: '请输入部门名称', trigger: 'blur' }
          ],
          deptMan:  [
            { required: true, message: '请输入联系人', trigger: 'blur' }
          ],
          deptAddress: [
            { required: true, message: '请输入部门地址', trigger: 'blur' }
          ],
          comId: [
            { required: true, message: '请输入所属公司的ID', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      searchCompanyList (query) {
        if (query) {
          var params = new URLSearchParams();
          params.append('comName', query);
          this.$http.get(api.COMPANY_SELECT+'?'+params).then( res => {
            this.companyOptions = res.data.data.data;
          })
        } else {
          this.$http.get(api.COMPANY_SELECT).then( res => {
            this.companyOptions = res.data.data.data;
          })
        }
      },
      handleSizeChange(val) {
        this.pagesize = val;
        console.log(val);
        this.load();
      },
      handleCurrentChange(val) {
        this.nowpage = val;
        console.log(val);
        this.load();
      },
      handleSelectionChange(selection) {
        this.multipleSelection = selection;
        if(selection.length === 1) {
          if (selection[0].status === 0) {
            this.isEnable = true;
            this.isDisable = false;
          } else if (selection[0].status === 1) {
            this.isEnable = false;
            this.isDisable = true;
          }
        } else {
          this.isEnable = true;
          this.isDisable = true;
        }
        console.log(this.multipleSelection);
      },
      updataStatus(status) {
        if (this.multipleSelection.length === 1) {
          var data = {};
          data.id = this.multipleSelection[0].id;
          if (status === 'enable') {
            data.status = 0;
          } else if(status === 'freeze') {
            data.status = 1;
          }

          var params = new URLSearchParams();
          params.append('id', this.multipleSelection[0].id);
          if (status === 'enable') {
            params.append('status', 0);
          } else if(status === 'freeze') {
            params.append('status', 1);
          }
          console.log(data);
          // $.ajax({
          //                   url :  api.DEPT_ENABLEORFREEZE,
          //                   data : data,
          //                   async : true,
          //                   contentType: "application/x-www-form-urlencoded",
          //                   type : 'POST',
          //                   success : function(data) {
          //                       console.log(data);
          //                   }
          //               });
          axios({
            method: "post",
            url: api.DEPT_ENABLEORFREEZE,
            data: data,
            // params: params,
            //  paramsSerializer: function(params) {
            //   return Qs.stringify(params, {arrayFormat: 'brackets'})
            //   // console.log(params);
            // },
          }) .then( res => {
            if (res.data.success) {
              this.$message({
                message: res.data.message,
                type: 'success'
              });
              // this.load();
              if (status === 'freeze') {
                this.isEnable = false;
                this.isDisable = true;
                this.multipleSelection[0].status = 1;
              } else if(status === 'enable'){
                this.isEnable = true;
                 this.isDisable = false;
                 this.multipleSelection[0].status = 0;
              }
            } else {
              this.$message({message: res.data.message});
            }
          }).catch(res => {
            this.$message({message: '操作失败！'});
          })
        } else if(this.multipleSelection.length > 1) {
          this.$message({message: "最多只能选择一条"});
        } else if(this.multipleSelection.length ===0) {
          this.$message({message: "请至少选择一条"});
        }
      },
      deleteCompany(){
        if (this.multipleSelection.length === 1) {
          this.$confirm('确认删除吗？').then(_ => {
            var params = new URLSearchParams();
            params.append('id', this.multipleSelection[0].id);
            this.$http.post(api.DEPT_DELETE + '?' + params).then(res => {
              if (res.data.success) {
                this.$message({
                  message: res.data.message,
                  type: 'success'
                });
                this.load();
              } else {
                this.$message({message: res.data.message});
              }
            }).catch( err => {
              this.$message({message: '删除失败！'});
            })
          }).catch(_ => {});
        } else if(this.multipleSelection.length > 1) {
          this.$message({message: "最多只能选择一条"});
        } else if(this.multipleSelection.length ===0) {
          this.$message({message: "请至少选择一条"});
        }
      },
      addOrEdit(type){
        if(type === 'add') {
          this.dialogFormVisible = true;
          this.dialogFormName = '新增部门';
          this.dialogForm.deptCode = '';
          this.dialogForm.deptName = '';
          this.dialogForm.deptMan = '';
          this.dialogForm.deptAddress = '';
          this.dialogForm.comId = '';
        } else if(type === 'edit'){
          if(this.multipleSelection.length === 1) {
            this.dialogFormVisible = true;
            this.dialogFormName = '编辑部门';
            this.dialogForm.id = this.multipleSelection[0].id;
            this.dialogForm.deptCode = this.multipleSelection[0].deptCode;
            this.dialogForm.deptName = this.multipleSelection[0].deptName;
            this.dialogForm.deptMan = this.multipleSelection[0].deptMan;
            this.dialogForm.deptAddress = this.multipleSelection[0].deptAddress;
            this.dialogForm.remark = this.multipleSelection[0].remark;
          } else if(this.multipleSelection.length === 0) {
            this.$message({message: '请至少选择一条进行编辑'});
          } else {
            this.$message({message: '最多只能选择一条进行编辑'});
          }
        }
      },
      submitForm(formName){
        var APIType = 'DEPT_INSERT';
        console.log(this.dialogFormName);
        if(this.dialogFormName === '新增部门') {
          APIType = 'DEPT_INSERT';
        } else if(this.dialogFormName === '编辑部门') {
          APIType = 'DEPT_UPDATE';

          // this.$http.get(api.DEPT_SELECT+'?'+params).then( res => {
          //   this.tableData = res.data.data.data;
          // })
        }
        // if(this.dialogFormName === '新增部门') {
          this.$refs[formName].validate( (valid) => {
            if(valid) {
              var params = new URLSearchParams();
              params.append('deptCode', this.dialogForm.deptCode);
              params.append('deptName', this.dialogForm.deptName);
              params.append('deptMan', this.dialogForm.deptMan);
              // params.append('contactWay', this.dialogForm.contactWay);
              params.append('deptAddress', this.dialogForm.deptAddress);
              params.append('comId', this.dialogForm.comId);
              // params.append('status', this.dialogForm.status);
              if(this.dialogForm.remark) {
                params.append('remark', this.dialogForm.remark);
              }
              if(this.dialogForm.id) {
                params.append('id', this.dialogForm.id);
              }
              this.$http.post(api[APIType] + '?' +params).then(res => {
                if(res.data.success) {
                  this.dialogFormVisible = false;
                  this.$message({
                    message: res.data.message,
                    type: 'success'
                  });
                  this.load();
                } else {
                  this.$message({message: res.data.message});
                }
              }).catch( err => {
                this.$message({
                    message: err.data.message
                  });
              })
            }
          })
        // }
      },
      search(){
        var params = new URLSearchParams();
        var paramsNum = 0;
        if(this.searchItem.comName) {
          // params.append('comName', this.searchItem.comName);
          paramsNum ++;
        } 
        if(this.searchItem.deptName) {
          // params.append('deptName', this.searchItem.deptName);
          paramsNum ++;
        }
        if(this.searchItem.deptMan) {
          // params.append('deptMan', this.searchItem.linkMan);
          paramsNum ++;
        }
        this.load(params, paramsNum);
      },
      load(params, paramsNum){
        console.log("paramsNum === ", paramsNum);
        if (!paramsNum || paramsNum === 0) {
          var params = new URLSearchParams();
          params.append('page', this.nowpage);
          params.append('rows', this.pagesize);
          this.$http.get(api.DEPT_SELECT+'?'+params).then( res => {
            console.log(res.data);
            this.tableData = res.data.data.data;
            this.total = res.data.data.total;
          })
        } else if (paramsNum > 0) {
          if(this.searchItem.comName) {
            params.append('comName', this.searchItem.comName);
          } 
          if(this.searchItem.deptName) {
            params.append('deptName', this.searchItem.deptName);
          }
          if(this.searchItem.deptMan) {
            params.append('deptMan', this.searchItem.linkMan);
          }
          params.append('page', this.nowpage);
          params.append('rows', this.pagesize);
          this.$http.get(api.DEPT_SELECT+'?'+params).then( res => {
            console.log(res.data);
            this.tableData = res.data.data.data;
            this.total = res.data.data.total;
          })
        }
      }
    },
    created(){
      this.load();
    }
  }
</script>

<style>
  .render-content {
    float: right;
    margin-right: 20px
  }

  .render-content i.fa {
    margin-left: 10px;
  }

  .render-content i.fa:hover{
    color: #e6000f;
  }

  .select-tree .el-scrollbar.is-empty .el-select-dropdown__list {
    padding: 0;
  }

  .select-tree .el-scrollbar {
    border: 1px solid #d1dbe5;
  }

  .select-tree .el-tree{
    border:0;
  }

</style>



