<template>

  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-button type="primary" icon="plus" @click="newAdd">新增</el-button>
      <el-button type="danger" icon="delete" @click="batchDelete">删除</el-button>
    </h3>
    <el-row slot="body" :gutter="24" style="margin-bottom: 20px;">
      <el-col :span="6" :xs="24" :sm="24" :md="6" :lg="6" style="margin-bottom: 20px;">
        <el-tree v-if="menuTree"
                 ref="menuTree"
                 :data="menuTree"
                 show-checkbox
                 highlight-current
                 :render-content="renderContent"
                 @node-click="handleNodeClick" clearable node-key="id" :props="defaultProps"></el-tree>
      </el-col>
      <el-col :span="18" :xs="24" :sm="24" :md="18" :lg="18">
        <el-card class="box-card">
          <div class="text item">
            <el-form :model="form" ref="form">
              <el-form-item label="父级" :label-width="formLabelWidth">
                <!--<el-input v-model="form.parentId" auto-complete="off"></el-input>-->
                <el-select-tree v-model="form.parentId" :treeData="menuTree" :propNames="defaultProps" clearable
                                placeholder="请选择父级">
                </el-select-tree>
              </el-form-item>
              <el-form-item label="名称" :label-width="formLabelWidth">
                <el-input v-model="form.menuName" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="链接" :label-width="formLabelWidth">
                <el-input v-model="form.url" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="编码" :label-width="formLabelWidth">
                <el-input v-model="form.menuCode" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="系统ID" :label-width="formLabelWidth">
                <el-input v-model="form.sysId" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="类型" :label-width="formLabelWidth">
                <el-input v-model="form.menuType" auto-complete="off"></el-input>
                <!-- <el-radio class="radio" v-model="form.menuType" label="0">菜单</el-radio>
                <el-radio class="radio" v-model="form.menuType" label="1">按钮</el-radio> -->
              </el-form-item>
              <el-form-item label="是否显示" :label-width="formLabelWidth">
                <el-radio class="radio" v-model="form.isShow" label="1">显示</el-radio>
                <el-radio class="radio" v-model="form.isShow" label="0">不显示</el-radio>
              </el-form-item>
              <!-- <el-form-item label="图标" :label-width="formLabelWidth">
                <i :class="form.icon" v-model="form.icon"></i>
                <el-button type="text" @click="selectIconDialog=true">选择</el-button>
              </el-form-item> -->
              <el-form-item label="排序" :label-width="formLabelWidth">
                <el-slider v-model="form.menuSort"></el-slider>
              </el-form-item>
              <el-form-item label="备注" :label-width="formLabelWidth">
                <el-input type="textarea" v-model="form.remark"></el-input>
              </el-form-item>
              <el-form-item label="" :label-width="formLabelWidth">
                <el-button type="primary" @click="onSubmit" v-text="form.id?'修改':'新增'"></el-button>
                <el-button type="danger" @click="deleteSelected" icon="delete" v-show="form.id && form.id!=null">删除
                </el-button>
              </el-form-item>
            </el-form>
          </div>
          <el-dialog title="选择图标" v-model="selectIconDialog" size="tiny">
            <div class="select-tree">
              <el-scrollbar
                tag="div"
                class='is-empty'
                wrap-class="el-select-dropdown__wrap"
                view-class="el-select-dropdown__list">
                
              </el-scrollbar>
            </div>
            <span slot="footer" class="dialog-footer">
          <el-button @click="selectIconDialog = false">取 消</el-button>
          <el-button type="primary" @click="selectIconDialog = false">确 定</el-button>
          </span>
          </el-dialog>
        </el-card>
      </el-col>
    </el-row>
  </imp-panel>

</template>
<script  type="text/babel">

  import panel from "../../components/panel.vue"
  import selectTree from "../../components/selectTree.vue"
  import treeter from "../../components/treeter"
  import merge from 'element-ui/src/utils/merge';

  import * as api from "../../api"

  export default {
    mixins: [treeter],
    components: {
      'imp-panel': panel,
      'el-select-tree': selectTree
    },
    data(){
      return {
        selectIconDialog:false,
        formLabelWidth: '100px',
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        maxId:7000000,
        menuTree: [],
        form: {
          id: null,
          name: '',
          sort: 0,
          icon:'',
          href: '',
          isShow: '',
          delivery: false,
          parentId: null,
          desc: ''
        }
      }
    },
    methods: {
      selectIcon(event){
        this.form.icon = event.target.className;
        this.selectIconDialog = false;
      },
      renderContent(h, {node, data, store}) {
        return (
          <span>
            <span>
              <span><i class={data.icon}></i>&nbsp;{node.label}</span>
            </span>
          </span>);
      },
      newAdd(){
        this.form = {
          id: null,
          name: '',
          sort: 0,
          icon:'',
          href: '',
          isShow: '',
          delivery: false,
          parentId: null,
          desc: ''
        };
      },
      deleteSelected(){
        if(api.CONTEXT == "./power-web") {
          var params = new URLSearchParams();
          params.append("id", this.form.id);
          this.$http.post(api.MENU_DELETE+'?' + params).then( res => {
            var data = res.data;
            this.$message(data.message);
            if(data.success) {
              this.deleteFromTree(this.menuTree, this.form.id);
              this.newAdd();
            } 
          })
        } else {
          var data = {"data":null,"success":true,"message":"删除成功"};
          this.$message(data.message);
          this.deleteFromTree(this.menuTree, this.form.id);
          this.newAdd();
        }
        // this.$http.get(api.SYS_MENU_DELETE + "?menuIds=" + this.form.id)
        //   .then(res => {
        //     this.$message('操作成功');
        //     this.deleteFromTree(this.menuTree, this.form.id);
        //     this.newAdd();
        //   }).catch(e =>{
        //     this.$message('操作成功');
        //     this.deleteFromTree(this.menuTree, this.form.id);
        //     this.newAdd();
        // })
      },
      batchDelete(){
        var checkKeys = this.$refs.menuTree.getCheckedKeys();
        if (checkKeys == null || checkKeys.length <= 0) {
          this.$message.warning('请选择要删除的资源');
          return;
        }
        this.$confirm('确定删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteSelected();
        });

      },
      handleNodeClick(data){
        console.log(data);
        this.form = merge({}, data);
        console.log(this.form);
      },
      onSubmit(){
        if(api.CONTEXT == "./power-web") {
          // id为空，即为菜单新增
          if(this.form.id == null ) {
             var params = new URLSearchParams();
            params.append("menuCode", this.form.menuCode);
            params.append("menuName", this.form.menuName);
            params.append("url", this.form.url);
            params.append("parentId", this.form.parentId?this.form.parentId : 0 );
            params.append("menuType", this.form.menuType);
            params.append("sysId", this.form.sysId);
            params.append("menuSort", this.form.menuSort);
            params.append("remark", this.form.remark);
            // params = params.toString();
            this.$http.post(api.MENU_INSERT + '?' + params ).then( res => {
              var data = res.data;
              this.$message(data.message);
              if(data.success) {
                //  需要从后台获取新增菜单的id
                // var searchParams = new URLSearchParams();
                // searchParams.append("menuCode",this.form.menuCode);
                // searchParams.append("menuName", this.form.menuName);
                // searchParams.append("url", this.form.url);
                // searchParams.append("parentId", this.form.parentId);
                // this.$http.get(api.MENU_SELECT, searchParams).then( rest => {
                //   this.form.id = rest.data.data.data.id;
                //   this.appendTreeNode(this.menuTree, rest.data.data.data);
                // })
                this.load();
              }
            })
          } else {
          //  如果id不为空，则为菜单修改
            var params = new URLSearchParams();
            params.append("id", this.form.id);
            params.append("menuCode", this.form.menuCode);
            params.append("menuName", this.form.menuName);
            params.append("url", this.form.url);
            params.append("parentId", this.form.parentId);
            params.append("menuType", this.form.menuType);
            params.append("sysId", this.form.sysId);
            params.append("menuSort", this.form.menuSort);
            params.append("remark", this.form.remark);
            this.$http.post(api.MENU_UPDATE+'?' + params).then( res => {
              var data = res.data;
              this.$message(data.message);
              if(data.success) {
                this.load();
              }
            })
          }
        } else {
          this.$message("操作成功"); 
          // this.load();
        }
      },
      /**
       * [将平行的菜单数据结构，转化为嵌套的菜单数据结构]
       * @param  {[array]}  data     [需要转化的菜单数据]
       * @param  {[string]} parentId [父级ID]
       * @return {[array]}           [转化后的菜单数据]
       */
      changeDataType(data, parentId) {
        var dataClone = [];
        for (var i = .0; i < data.length; i++) {
          // dataClone = cons
        }
        var tree = [];
        var temp;
        for (var i = 0; i < data.length; i++) {
            if (data[i].parentId == parentId) {
                var obj = data[i];
                temp = this.changeDataType(data, data[i].id);
                if (temp.length > 0) {
                 obj.children = temp;
                }
                tree.push(obj);
            }
        }
        return tree;
      },
      load(){
        // 如果是本地环境，获取后台数据进行渲染
        if(api.CONTEXT === "./power-web") {
          this.$http.get(api.MENU_SELECT).then( res => {
            console.log(res.data);
            var data = res.data.data.data;
            for(var i=0; i < data.length; i++ ) {
              data[i].label = data[i].menuName;
              data[i].name = data[i].menuName;
            }
            this.menuTree = this.changeDataType(data, 0);
          })
        } else {
          // 如果是本地环境，用假数据填充
          var data = {"data":{"data":[
            {"id":1,"isDeleted":0,"menuCode":"01","menuName":"权限","url":"/power/menu/select","parentId":0,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":2,"isDeleted":0,"menuCode":"01","menuName":"菜单管理","url":"/power/menu/select","parentId":1,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":3,"isDeleted":0,"menuCode":"01","menuName":"菜单列管理","url":"/power/menu/select","parentId":1,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":4,"isDeleted":0,"menuCode":"01","menuName":"角色管理","url":"/power/menu/select","parentId":1,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":5,"isDeleted":0,"menuCode":"01","menuName":"组织结构","url":"/power/menu/select","parentId":0,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":6,"isDeleted":0,"menuCode":"01","menuName":"公司","url":"/power/menu/select","parentId":5,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":7,"isDeleted":0,"menuCode":"01","menuName":"部门","url":"/power/menu/select","parentId":5,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":8,"isDeleted":0,"menuCode":"01","menuName":"人员","url":"/power/menu/select","parentId":5,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":9,"isDeleted":0,"menuCode":"01","menuName":"品牌","url":"/power/menu/select","parentId":5,"menuType":0,"sysId":1,"menuSort":1,"remark":""},
            {"id":10,"isDeleted":0,"menuCode":"01","menuName":"仓库","url":"/power/menu/select","parentId":5,"menuType":0,"sysId":1,"menuSort":1,"remark":""}
            ],"total":1,"nowpage":0,"pagesize":0,"rowStart":0,"rowEnd":10,"order":"desc","sort":"id","condition":null},"success":true,"message":"成功"}
        };
        data = data.data.data;
        for(var i=0; i < data.length; i++ ) {
              data[i].label = data[i].menuName;
              data[i].name = data[i].menuName;
          }
        this.menuTree.push(...this.changeDataType(data, 0));
      }
    },
    created(){
      this.load();
    }
  }
</script>

<style>
  .select-tree .icons-wrapper{
    display: block;
  }
  .select-tree .is-empty i{
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    display: inline-block;
    cursor: pointer;
  }
  .select-tree .is-empty i:hover{
    background-color: #0d6aad;
    color: #ffffff;
  }

</style>



