<template>

  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-form :inline="true" :model="form" ref="form">
        <el-form-item label="角色编号">
          <el-input v-model="searchItem.no" placeholder="请输入角色编号" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="角色名称">
          <el-input v-model="searchItem.name" placeholder="请输入角色名称" icon="search"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
        <el-form-item style="float: right;">
          <el-button type="primary" icon="plus" @click="addRole('add')">添加功能角色</el-button>
        </el-form-item>
      </el-form>
    </h3>
    <el-row slot="body" :gutter="24" style="margin-bottom: 20px;">
    <el-table :data="tableData" style="width: 100%;">
      <el-table-column prop="roleCode" label="角色编号" width="180"></el-table-column>
      <el-table-column prop="roleName" label="角色名称" width="180"></el-table-column>
      <el-table-column prop="remark" label="备注"></el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-popover ref="popoverDetail" placement="top-start" width="200" trigger="click">
            <el-form :model="form">
              <el-form-item label="角色编号：" style="margin-bottom: 0px;">
                <span v-model="form.no">12121</span>
              </el-form-item>
              <el-form-item label="角色名称：" style="margin-bottom: 0px;">
                <span v-model="form.name">琪哥</span>
              </el-form-item>
              <el-form-item label="角色描述：" style="margin-bottom: 0px;">
                <span type="textarea" v-model="form.remarks">真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅真帅</span>
              </el-form-item>
            </el-form>
          </el-popover>
          <el-button size="small" icon="document" v-popover:popoverDetail>查看</el-button>
          <el-button size="small" icon="edit" @click="addRole('edit', scope.$index, tableData)">编辑</el-button>
          <el-button size="small" type="danger" icon="delete" @click="deleteRole(scope.$index, tableData)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-row>
  </imp-panel>

</template>
<script>

  import panel from "../../components/panel.vue"
  import selectTree from "../../components/selectTree.vue"
  import treeter from "../../components/treeter"


  import * as api from "../../api"

  export default {
    mixins: [treeter],
    components: {
      'imp-panel': panel,
      'el-select-tree': selectTree,
    },
    data(){
      return {
        form: {
          id: null,
          no: '',
          name: '',
          remarks: '',
        },
        searchItem: {
          name: "",
          no: ""
        },
        tableData: [],
      }
    },
    methods: {
      addRole(style){
        this.$router.push({path: 'roleAddOrEdit', query: {id: style}});
      },
      deleteRole(index, rows){
        // 如果eclipse开发环境，后台获取数据进行渲染
        if( api.CONTEXT === "./power-web") {
          var roleId = rows[index].id;
          var params = new URLSearchParams();
          params.append("roleCode", this.form.no);
          this.$http.post(api.ROLE_DELETE, params).then(res => {
            var data = res.data;
            var isSuccess = data.success;
            if(data.success) {
              rows.splice(index, 1);
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            } else {
              this.$message.error('删除失败');
            }
          })
        } else {
        // 如果是本地环境，假数据判断
          var data = {"data":null,"success":true,"message":"删除成功"};
          var isSuccess = data.success;
          if(data.success) {
            rows.splice(index, 1);
            this.$message({
              message: '删除成功',
              type: 'success'
            });
          } else {
            this.$message.error('删除失败');
          }
        }
      },
      search(){
        var params = new URLSearchParams();
        params.append("roleCode", this.searchItem.no);
        params.append("roleName", this.searchItem.name);
        this.load(params);
      },
      load(params){
        // 如果eclipse开发环境，后台获取数据进行渲染
        if(api.CONTEXT === "./power-web"){
          this.$http.get(api.ROLE_SELECT,params).then( res => {
            console.log(res.data);
            this.tableData = res.data.data.data;
          })
        } else {
        // 如果是本地环境，假数据填充
          var data = {"data":{"data":[{"id":27,"isDeleted":0,"roleCode":"005","roleName":"你","sysId":null,"remark":null},{"id":25,"isDeleted":0,"roleCode":"004","roleName":"晨魔","sysId":null,"remark":null},{"id":23,"isDeleted":0,"roleCode":"003","roleName":"晨鬼","sysId":null,"remark":null},{"id":21,"isDeleted":0,"roleCode":"002","roleName":"琪魔","sysId":null,"remark":null},{"id":19,"isDeleted":0,"roleCode":"001","roleName":"琪鬼","sysId":null,"remark":null},{"id":17,"isDeleted":0,"roleCode":"111","roleName":"1","sysId":null,"remark":null},{"id":15,"isDeleted":0,"roleCode":"111","roleName":"1","sysId":null,"remark":null},{"id":13,"isDeleted":0,"roleCode":"111","roleName":"1","sysId":null,"remark":null},{"id":11,"isDeleted":0,"roleCode":"1111","roleName":"11","sysId":null,"remark":null},{"id":9,"isDeleted":0,"roleCode":"06","roleName":"娴嬭瘯瑙掕壊","sysId":null,"remark":"娴嬭瘯"}],"total":14,"nowpage":0,"pagesize":0,"rowStart":0,"rowEnd":10,"order":"desc","sort":"id","condition":null},"success":true,"message":"成功"};
          this.tableData = data.data.data;
        }
      }
    },
    created(){
      this.load();
    }
  }
</script>

<style>
  .render-content {
    float: right;
    margin-right: 20px
  }

  .render-content i.fa {
    margin-left: 10px;
  }

  .render-content i.fa:hover{
    color: #e6000f;
  }

  .select-tree .el-scrollbar.is-empty .el-select-dropdown__list {
    padding: 0;
  }

  .select-tree .el-scrollbar {
    border: 1px solid #d1dbe5;
  }

  .select-tree .el-tree{
    border:0;
  }

</style>



