<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-form :inline="true" :model="form" ref="form">
        <el-form-item label="公司名">
          <el-input v-model="searchItem.comName" placeholder="请输入公司名称" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="公司简称">
          <el-input v-model="searchItem.comShortName" placeholder="请输入公司名称" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="联系人">
          <el-input v-model="searchItem.linkMan" placeholder="请输入角色名称" icon="search"></el-input>
        </el-form-item>
        <el-form-item label="联系方式">
          <el-input v-model="searchItem.contactWay" placeholder="请输入角色名称" icon="search"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
       <!--  <el-form-item style="float: right;">
          <el-button type="primary" icon="plus" @click="addRole('add')">添加功能角色</el-button>
        </el-form-item> -->
      </el-form>
    </h3>
    <el-row slot="body" :gutter="24" style="margin-bottom: 20px;">
      <div style="margin-left: 15px; margin-bottom: 15px;">
        <el-button type="primary" @click="addOrEdit('add')">新增</el-button>
        <el-button type="primary" @click="addOrEdit('edit')">编辑</el-button>
        <el-dialog :title="dialogFormName" :visible.sync="dialogFormVisible" size="tiny">
          <el-form :model="dialogForm"  :rules='rules' ref='dialogForm' >
            <el-form-item label="公司编码" :label-width="formLabelWidth" prop='comCode'>
              <el-input v-model="dialogForm.comCode" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="公司名称" :label-width="formLabelWidth" prop='comName'>
              <el-input v-model="dialogForm.comName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="注册资金" :label-width="formLabelWidth" prop='registerCapital'>
              <el-input v-model="dialogForm.registerCapital" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="公司性质" :label-width="formLabelWidth" prop='status'>
              <el-radio-group v-model='dialogForm.property'>
              <el-radio  :label="0">贸易型</el-radio>
              <el-radio  :label="1">工程型</el-radio>
            </el-radio-group>
            </el-form-item>
            <el-form-item label="付款方式" :label-width="formLabelWidth" prop='status'>
              <!-- <el-radio-group v-model='dialogForm.payType'>
                <el-radio  :label="0">现金</el-radio>
                <el-radio  :label="1">月结</el-radio>
              </el-radio-group> -->
              <el-radio class="radio" v-model="dialogForm.payType" label="0">现金</el-radio>
              <el-radio class="radio" v-model="dialogForm.payType" label="1">月结</el-radio>
            </el-form-item>
            <el-form-item label="状态" :label-width="formLabelWidth" prop='status'>
              <!-- <el-radio-group v-model='dialogForm.status'>
                <el-radio  :label="0">启用</el-radio>
                <el-radio  :label="1">冻结</el-radio>
              </el-radio-group> -->
              <el-radio class="radio" v-model="dialogForm.status" label="0" >启用</el-radio>
              <el-radio class="radio" v-model="dialogForm.status" label="1">冻结</el-radio>
            </el-form-item>
            <el-form-item label="备注" :label-width="formLabelWidth">
              <el-input v-model="dialogForm.remark" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="submitForm('dialogForm')">确 定</el-button>
          </div>
        </el-dialog>
        <el-button type="primary" @click="updataStatus('enable')" :disabled='isEnable'>启用</el-button>
        <el-button type="primary" @click="updataStatus('freeze')" :disabled='isDisable'>禁用</el-button>
        <el-button type="primary" @click="deleteCompany()">删除</el-button>
        <el-button type="primary" @click="edit()">导出</el-button>
      </div>
    <el-table :data="tableData" style="width: 100%;" ref="multipleTable" border tooltip-effect="dark" :fit="true"  @selection-change='handleSelectionChange' height="445">
      <el-table-column type="selection" width="50" ></el-table-column> 
      <el-table-column prop="comName" label="公司名称" ></el-table-column> 
      <el-table-column prop="comShortName" label="公司简称" ></el-table-column>
      <el-table-column prop="registerCapital" label="注册资金" ></el-table-column> 
      <el-table-column prop="makeSize" label="产值" ></el-table-column>
      <el-table-column prop="mail" label="邮箱" ></el-table-column>
      <el-table-column prop="linkMan" label="联系人"></el-table-column>
      <el-table-column prop="contactWay" label="联系方式" ></el-table-column>
      <el-table-column prop="address" label="地址" ></el-table-column>
      <el-table-column prop="remark" label="备注" ></el-table-column>
    </el-table>
    <el-pagination
      style="margin-top: 15px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="nowpage"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-row>
  </imp-panel>

</template>
<script>

  import panel from "../../components/panel.vue"
  import selectTree from "../../components/selectTree.vue"
  import treeter from "../../components/treeter"


  import * as api from "../../api"

  export default {
    mixins: [treeter],
    components: {
      'imp-panel': panel,
      'el-select-tree': selectTree,
    },
    data(){
      return {
        pagesize: 10,  // 每页10条数据
        total: 1000,  // 一共1000条数据
        nowpage: 1, // 当前第几页，后台是从1开始计数的
        form: {
          id: null,
          no: '',
          name: '',
          remarks: '',
        },
        searchItem: {
          comName: "",
          comShortName:'',
          linkMan: '',
          contactWay: ''
        },
        tableData: [],
        multipleTable: [],
        multipleSelection:[], // 表格多选数据
        dialogFormVisible: false, // 新增/编辑弹出层是否可见
        dialogFormName: '',  // 新增/编辑弹出层的title
        formLabelWidth: '80px', // 新增/编辑弹出层的宽度
        isDisable: true,    //"禁用"按钮是否禁用
        isEnable: true,   // "启用"按钮是否禁用
        dialogForm:{  // 弹出层表单数据
          id:'',
          comCode: '',
          comName: '',
          registerCapital: '',
          property: "0",
          payType: "0",
          status: "0",
          remark: ''
        },   // 新增/编辑弹出层的表单数据
        rules: {   //表单校验规则
          comCode: [
            { required: true, message: '请输入公司编码', trigger: 'blur' },
            { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
          ],
          comName: [
            { required: true, message: '请输入公司名称', trigger: 'blur' }
          ],
          registerCapital:  [
            { required: true, message: '请输入注册资金', trigger: 'blur' }
          ],
          status: [
            { required: true, message: '请选择状态', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleSizeChange(val) {
        this.pagesize = val;
        console.log(val);
        this.load();
      },
      handleCurrentChange(val) {
        this.nowpage = val;
        console.log(val);
        this.load();
      },
      handleSelectionChange(selection) {
        this.multipleSelection = selection;
        if(selection.length === 1) {
          if (selection[0].status === 0) {
            this.isEnable = true;
            this.isDisable = false;
          } else if (selection[0].status === 1) {
            this.isEnable = false;
            this.isDisable = true;
          }
        } else {
          this.isEnable = true;
          this.isDisable = true;
        }
        console.log(this.multipleSelection);
      },
      deleteCompany(){
        if (this.multipleSelection.length === 1) {
          this.$confirm('确认删除吗？').then(_ => {
            var params = new URLSearchParams();
            params.append('id', this.multipleSelection[0].id);
            this.$http.post(api.COMPANY_DELETE + '?' + params).then(res => {
              if (res.data.success) {
                this.$message({
                  message: res.data.message,
                  type: 'success'
                });
                this.load();
              } else {
                this.$message({message: res.data.message});
              }
            }).catch( err => {
              this.$message({message: '删除失败！'});
            })
          }).catch(_ => {});
        } else if(this.multipleSelection.length > 1) {
          this.$message({message: "最多只能选择一条"});
        } else if(this.multipleSelection.length ===0) {
          this.$message({message: "请至少选择一条"});
        }
      },
      updataStatus(status) {
        if (this.multipleSelection.length === 1) {
          var params = new URLSearchParams();
          params.append('id', this.multipleSelection[0].id);
          if (status === 'enable') {
            params.append('status', 0);
          } else if(status === 'freeze') {
            params.append('status', 1);
          }
          this.$http.post(api.COMPANY_ENABLEORFREEZE+'?'+params).then( res => {
            if (res.data.success) {
              this.$message({
                message: res.data.message,
                type: 'success'
              });
              // this.load();
              if (status === 'freeze') {
                this.isEnable = false;
                this.isDisable = true;
                this.multipleSelection[0].status = 1;
              } else if(status === 'enable'){
                this.isEnable = true;
                 this.isDisable = false;
                 this.multipleSelection[0].status = 0;
              }
            } else {
              this.$message({message: res.data.message});
            }
          }).catch(res => {
            this.$message({message: '操作失败！'});
          })
        } else if(this.multipleSelection.length > 1) {
          this.$message({message: "最多只能选择一条"});
        } else if(this.multipleSelection.length ===0) {
          this.$message({message: "请至少选择一条"});
        }
      },
      addOrEdit(type){
        if(type === 'add') {
          this.dialogFormVisible = true;
          this.dialogFormName = '新增公司';
          this.dialogForm.comCode = '';
          this.dialogForm.comName = '';
          this.dialogForm.registerCapital = '';
          this.dialogForm.property = '0';
          this.dialogForm.payType = '0';
          this.dialogForm.status = '0';
        } else if(type === 'edit'){
          if(this.multipleSelection.length === 1) {
            this.dialogFormVisible = true;
            this.dialogFormName = '编辑公司';
            this.dialogForm.id = this.multipleSelection[0].id;
            this.dialogForm.comCode = this.multipleSelection[0].comCode;
            this.dialogForm.comName = this.multipleSelection[0].comName;
            this.dialogForm.registerCapital = this.multipleSelection[0].registerCapital;
            this.dialogForm.property = this.multipleSelection[0].property;
            this.dialogForm.payType = this.multipleSelection[0].payType;
            this.dialogForm.status = this.multipleSelection[0].status;
            this.dialogForm.remark = this.multipleSelection[0].remark;
          } else if(this.multipleSelection.length === 0) {
            this.$message({message: '请至少选择一条进行编辑'});
          } else {
            this.$message({message: '最多只能选择一条进行编辑'});
          }
        }
      },
      submitForm(formName){
        var APIType = 'COMPANY_INSERT';
        console.log(this.dialogFormName);
        if(this.dialogFormName === '新增公司') {
          APIType = 'COMPANY_INSERT';
        } else if(this.dialogFormName === '编辑公司') {
          APIType = 'COMPANY_UPDATE';

          // this.$http.get(api.COMPANY_SELECT+'?'+params).then( res => {
          //   this.tableData = res.data.data.data;
          // })
        }
        // if(this.dialogFormName === '新增公司') {
          this.$refs[formName].validate( (valid) => {
            if(valid) {
              var params = new URLSearchParams();
              params.append('comCode', this.dialogForm.comCode);
              params.append('comName', this.dialogForm.comName);
              params.append('registerCapital', this.dialogForm.registerCapital);
              // params.append('contactWay', this.dialogForm.contactWay);
              params.append('property', this.dialogForm.property);
              params.append('payType', this.dialogForm.payType);
              params.append('status', this.dialogForm.status);
              if(this.dialogForm.remark) {
                params.append('remark', this.dialogForm.remark);
              }
              if(this.dialogForm.id) {
                params.append('id', this.dialogForm.id);
              }
              this.$http.post(api[APIType] + '?' +params).then(res => {
                if(res.data.success) {
                  this.dialogFormVisible = false;
                  this.$message({
                    message: res.data.message,
                    type: 'success'
                  });
                  this.load();
                } else {
                  this.$message({message: res.data.message});
                }
              }).catch( err => {
                this.$message({
                    message: err.data.message
                  });
              })
            }
          })
        // }
      },
      addRole(style){
        this.$router.push({path: 'roleAddOrEdit', query: {id: style}});
      },
      deleteRole(index, rows){
        // 如果eclipse开发环境，后台获取数据进行渲染
        if( api.CONTEXT === "./power-web") {
          var roleId = rows[index].id;
          var params = new URLSearchParams();
          params.append("roleCode", this.form.no);
          this.$http.post(api.ROLE_DELETE, params).then(res => {
            var data = res.data;
            var isSuccess = data.success;
            if(data.success) {
              rows.splice(index, 1);
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            } else {
              this.$message.error('删除失败');
            }
          })
        } else {
        // 如果是本地环境，假数据判断
          var data = {"data":null,"success":true,"message":"删除成功"};
          var isSuccess = data.success;
          if(data.success) {
            rows.splice(index, 1);
            this.$message({
              message: '删除成功',
              type: 'success'
            });
          } else {
            this.$message.error('删除失败');
          }
        }
      },
      search(){
        var paramsNum = 0;
        if(this.searchItem.comName) {
          paramsNum ++;
        }
        if(this.searchItem.comShortName) {
          paramsNum ++;
        }
        if(this.searchItem.linkMan) {
          paramsNum ++;
        }
        if(this.searchItem.contactWay) {
          paramsNum ++;
        }
        this.load(paramsNum);
      },
      load(paramsNum){
        if(!paramsNum || paramsNum === 0) {
          var params = new URLSearchParams();
          params.append('page', this.nowpage);
          params.append('rows', this.pagesize);
          this.$http.get(api.COMPANY_SELECT+'?'+params).then( res => {
            console.log(res.data);
            this.tableData = res.data.data.data;
            this.total = res.data.data.total;
          })
        } else if (paramsNum > 0) {
          var params = new URLSearchParams();
          if(this.searchItem.comName) {
            params.append('comName', this.searchItem.comName);
          }
          if(this.searchItem.comShortName) {
            params.append('comShortName', this.searchItem.comShortName);
          }
          if(this.searchItem.linkMan) {
            params.append('linkMan', this.searchItem.linkMan);
          }
          if(this.searchItem.contactWay) {
            params.append('contactWay', this.searchItem.contactWay);
          }
          params.append('page', this.nowpage);
          params.append('rows', this.pagesize);
          this.$http.get(api.COMPANY_SELECT+'?'+params).then( res => {
            console.log(res.data);
            this.tableData = res.data.data.data;
            this.total = res.data.data.total;
          })
        }
      }
    },
    created(){
      this.load();
    }
  }
</script>

<style>
  .render-content {
    float: right;
    margin-right: 20px
  }

  .render-content i.fa {
    margin-left: 10px;
  }

  .render-content i.fa:hover{
    color: #e6000f;
  }

  .select-tree .el-scrollbar.is-empty .el-select-dropdown__list {
    padding: 0;
  }

  .select-tree .el-scrollbar {
    border: 1px solid #d1dbe5;
  }

  .select-tree .el-tree{
    border:0;
  }

</style>



